<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-10" class="modal-title">{{ ScreenshotDetailArray.screenshoT_TITLE }}</h5>
                            <button class="close" v-on:click="closePopup">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div id="divDialogDetails" class="modal-body"
                            style="height: auto; min-height: 0px; max-height: none;">
                            <div class="col-md-12 p-0">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-md-8 d-flex workdairy_leftbase">
                                            <div class="w-100 h-100 bg-light d-flex align-items-center">
                                                <img class="snapshot w-100 " loading="lazy" v-if="ScreenshotDetailArray.screenshoT_TYPE.toLowerCase() == 'screenshot' || ScreenshotDetailArray.screenshoT_TYPE.toLowerCase() == 'webcam' "
                                                    :src="'https://cogniterblob.blob.core.windows.net/'+ScreenshotDetailArray.containeR_NAME+'/screenshots/' + ScreenshotDetailArray.screenshoT_PATH"
                                                    title="Screen--NoTask" alt="Screen--NoTask">
                                                <video width="100%" height="420" class="border" controls="" v-if="ScreenshotDetailArray.screenshoT_TYPE == 'video'">
                                                   <source :src="ScreenshotDetailArray.screenshoT_PATH">
                                                </video>
                                            </div>
                                        </div>
                                        <div class="col-md-4 workdairy_rightbase">
                                            <div class="row">
                                                <div class="col-md-12 p-0" id="divapplicationdatadetails">
                                                    <div class="app-time p-0">
                                                        <div class="heading-border mb-2">
                                                            <h5 class="h5"><span>{{ $t('ApplicationTime') }}</span></h5>
                                                        </div>
                                                        <ul class="app-time-info  filterscroll mCustomScrollbar _mCS_10 mCS_no_scrollbar" style="max-height:240px" v-for="item in ScreenshotApplicationTime">
                                                            <div id="mCSB_10" class="mCustomScrollBox mCS-dark-thick mCSB_vertical mCSB_inside" style="max-height: none;" tabindex="0">
                                                                <div id="mCSB_10_container" class="mCSB_container mCS_y_hidden mCS_no_scrollbar_y" style="position:relative; top:0; left:0;" dir="ltr">
                                                                    <li class="position-relative frm-outer-container">
                                                                        <div class="w-100 float-left d-flex pl-3 hs bg-grey py-1 btn-toggle cursor-pointer">
                                                                            <div :class="'appicon ' + (item.icoN_CLASS == null ? 'default' : item.icoN_CLASS ) "></div>
                                                                            <p :title="item.icoN_CLASS"
                                                                                style="min-width: 120px; max-width: 120px; cursor: pointer;"
                                                                                class="text-truncate thickbox "
                                                                                data-placement="left" href="javascript:;"
                                                                                data-toggle="modal"
                                                                                data-target="#updateentry">
                                                                                {{ item.applicatioN_NAME }}
                                                                            </p>
                                                                            <div class="progress prog_bx mt-2"
                                                                                :data-duration="item.timE_DURATION" :data-totalduration="item.totaL_DURATION">
                                                                                <div class="progress-bar bg-blue"
                                                                                    :style="'width:' + FindPercentageOfTotalHours(item.timE_DURATION,item.totaL_DURATION) + '%'"
                                                                                    role="progressbar" aria-valuenow="0"
                                                                                    aria-valuemin="0" aria-valuemax="100">
                                                                                </div>
                                                                            </div>
                                                                            <span data-duration="2"
                                                                                data-totalduration="2.00"
                                                                                class="spantimeDura mt-1">{{ secondsToTime(item.timE_DURATION) }}<b>({{ FindPercentageOfTotalHours(item.timE_DURATION,item.totaL_DURATION) + '%' }})</b></span>
                                                                        </div>
                                                                        <span class="d-block clearfix"></span>

                                                                    </li>
                                                                </div>
                                                                <div id="mCSB_10_scrollbar_vertical"
                                                                    class="mCSB_scrollTools mCSB_10_scrollbar mCS-dark-thick mCSB_scrollTools_vertical"
                                                                    style="display: none;">
                                                                    <div class="mCSB_draggerContainer">
                                                                        <div id="mCSB_10_dragger_vertical"
                                                                            class="mCSB_dragger"
                                                                            style="position: absolute; min-height: 30px; height: 0px; top: 0px;">
                                                                            <div class="mCSB_dragger_bar"
                                                                                style="line-height: 30px;"></div>
                                                                        </div>
                                                                        <div class="mCSB_draggerRail"></div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </ul>
                                                        <div class="table-responsive user-clicksin">
                                                            <table class="table table-border mb-0">
                                                                <tbody v-if="noRecordApplication">
                                                                    <tr>
                                                                        <td colspan="3" class="text-center text-danger no-record">
                                                                            {{$t('NoRecordfound')}}
                                                                        </td>
                                                                    </tr>
                                                                </tbody>
                                                            </table>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-md-12 p-0" id="diveventsperminutes">
                                                    <div class="heading-border mb-2 mt-3">
                                                        <h5 class="h5"><span>{{ $t('EventPerMin') }}</span></h5>
                                                    </div>
                                                </div>
                                                <div class="table-responsive user-clicksin">
                                                    <table class="table table-border mb-0">
                                                        <thead>
                                                            <tr>
                                                                <th>{{ $t('Time') }}</th>
                                                                <th>{{ $t('keyStrokes') }}</th>
                                                                <th>{{ $t('Mouse') }}</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody v-if="noRecord">
                                                            <tr>
                                                                <td colspan="3" class="text-center text-danger no-record">
                                                                    {{ $t('NoRecordfound') }}
                                                                </td>
                                                            </tr>
                                                        </tbody>
                                                        <tbody v-else>
                                                            <tr v-for="event in ScreenshotEventsPerMinute">
                                                                <td>{{ FormatDateToTime(event.time) }}</td>
                                                                <td>{{ event.keyStrokes }}</td>
                                                                <td>{{ event.mouseClicks }}</td>
                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-12 mt-3" id="ssDescription">
                                            <div class="heading-border"><h5 class="h5 mb-2"><span>Description</span></h5></div>
                                            <p>{{ ScreenshotDetailArray.screenshoT_TITLE }}</p>
                                        </div>
                                        <div class="col-md-12" id="commentData">
                                            <div id="Comments" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-2">
                                                <dynamic-form lang="en" :buttons="buttons" ref="PostCommentForm" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                                                <div id="CommentsData" class="listing" style="max-height: 465px;overflow-y: auto;">
                                                    <div id="accordion" class="w-100 activity-tab">
                                                        <div class="card" v-for="(item,index) in PostedCommentListing">
                                                            <div class="card-header">
                                                                <a class="card-link" data-toggle="collapse" :href="'#CommentAccordion'+index">
                                                                    <div class="conversation-date heading-border"><span>{{ $options.filters.formatDate(item.createdDate) }}</span>
                                                                    </div>
                                                                </a>
                                                            </div>
                                                            <div :id="'CommentAccordion'+i" class="collapse show" data-parent="#accordion">
                                                                <div class="card-body">
                                                                    <div class="conversation-main">
                                                                        <div class="conversation-time"><span>{{ FormatDateToTime(item.createdDate) }}</span></div>
                                                                        <div class="conversation-box-con">
                                                                            <div class="conversation-box">
                                                                                <div class="name-circle light-yellow">
                                                                                    <img class="img-circle avatar"
                                                                                        :src="'https://cogniterblob.blob.core.windows.net/'+item.containeR_NAME+'/Upload/UserProfileImage/'+item.userImage"
                                                                                        width="51" height="51" alt="Image">
                                                                                </div>
                                                                                <div class="conversation-header addednote-ticket">
                                                                                    <span>
                                                                                        <span class="sender-name"></span>
                                                                                        <span class="sender-department" :title="item.comments">
                                                                                            {{ item.comments }}</span>
                                                                                    </span>
                                                                                </div>
                                                                                <div class="conversation-content">
                                                                                    <div class="col-12 p-0 notesdata">
                                                                                        
                                                                                        <h6 class="text-muted time"><i><b>{{ item.createdBy }}</b>,
                                                                                                Posted on: <span class="text-danger"
                                                                                                    title="11/28/2023 05:31:25 PM UTC">{{  DateDifference(item.createdDate) }}</span> </i></h6>
                                                                                    </div>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-8 mb-4 mt-3">
                                            <div class="level_info" >
                                                <div class="progress prog_bx">
                                                    <div class="progress-bar bg-sky" :style="'width:' + GetDisplayScreenData.activity + '%'" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100"></div>
                                                </div>
                                                <ul> 
                                                    <li><b>Level:</b>{{GetDisplayScreenData.activity <= 20 ? 'Low' : GetDisplayScreenData.activity > 20 && GetDisplayScreenData.activity <= 60 ? 'Medium' : 'High'}}</li>
                                                    <li><b>KeyStrokes:</b>{{ GetDisplayScreenData.totaL_KEYSTROKES }}</li>
                                                    <li><b>Mouse:</b>{{ GetDisplayScreenData.totaL_MOUSE_CLICKS }}</li>
                                                    <li><b>Duration: </b><span class="durationval">{{ secondsToTime(GetDisplayScreenData.timE_DURATION) }}</span></li>
                                                    <li><b>Activity:</b>{{ GetDisplayScreenData.activity + '%' }}</li>
                                                </ul>
                                            </div>
                                        </div>
                                        <div class="col-md-4 mb-4 mt-3">
                                            <div class="legends_medium date_info text-center">
                                                <h2>{{ $options.filters.formatDate(ScreenShotData.InputDate) }}<br>{{ FormatDateToTime(ScreenshotDetailArray.createD_AT) }}</h2>
                                            </div>
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section></template>
<script>
import DataService from '../../services/DataService';
import moment from 'moment';
export default {
    props: {
        ScreenshotDetailArray: {
            type: Object
        },
        ScreenShotData:{
            type: Object
        }
    },
    data() {
        return {
            ScreenshotApplicationTime: [],
            ScreenshotEventsPerMinute: [],
            GetDisplayScreenData: [],
            noRecord:false,
            noRecordApplication:false,
            PostedCommentListing:[],
            FormSchema: [{
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Comment'),
                    name: "Comment",
                    value: "",
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                    },
                    validationRules: {
                        "required": true
                    },
                }]
            }],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                isVisible: true,
            }],
        }
    },
    created() {
        this.GetScreenshotApplicationTime();
        this.GetEventsPerMinutes();
        this.GetDisplayScreen();
        this.FetchCommentsData();
    },
    methods: {
        closePopup() {
            var vm = this;
            vm.$parent.CloseScreenshotDetails();
        },
        GetScreenshotApplicationTime(){
            var vm = this;
            var getsearchdate = null;
            if (vm.ScreenShotData.InputDate != null) {
                getsearchdate = vm.$options.filters.formatDate(vm.ScreenShotData.InputDate);
            }
            var params = `screenshotId=${vm.ScreenshotDetailArray.screenshoT_ID}&${vm.ScreenShotData.UserId}&${vm.ScreenShotData.ProjectId}&langkey=en&search=${getsearchdate}&duration=0&totalHours=0`;
            DataService.GetApplicationTracking(params).then(response=>{
                if(response.data.length > 0){
                    vm.ScreenshotApplicationTime = response.data;
                    vm.noRecordApplication = false;
                }else{vm.noRecordApplication = true;}
            }).catch(err=>{
                console.log(err);
            })
        },
        secondsToTime(secs){
            // TO Get Hours
            var hours = Math.floor(secs / (60 * 60));
            // To Get Minutes
            var divisor_for_minutes = secs % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            // To Get Seconds
            var divisor_for_seconds = divisor_for_minutes % 60;
            var seconds = Math.ceil(divisor_for_seconds);

            var obj = hours + 'H ' + minutes + 'M ' + seconds + 'S';
            return obj;
        },
        FindPercentageOfTotalHours(TimeDuration,TotalDuration){
            return ((TimeDuration/ TotalDuration) * 100).toFixed(2);
        },
        FormatDateToTime(Getdate) {
            return moment(Getdate).format('LT');
        },
        GetEventsPerMinutes(){
            var vm = this;
            var params = `screenshotId=${vm.ScreenshotDetailArray.screenshoT_ID}`;
            DataService.GetEventsPerMinutes(params).then(response=>{
                if(response.data.length > 0){
                    vm.ScreenshotEventsPerMinute = response.data;
                    vm.noRecord = false;
                }else{
                    vm.noRecord = true;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        onSubmit() {
            var vm = this;
            var params = `screenids=${vm.ScreenshotDetailArray.screenshoT_ID}&comment=${vm.FormSchema[0].Data[0].value}`;
            DataService.PostComment(params).then(response => {
                if (response.data) {
                    vm.FormSchema[0].Data[0].value = '';
                    vm.$refs.PostCommentForm.validateForm.reset();
                    vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"), 'success', true, vm.$t('Alert'));
                    vm.FetchCommentsData();
                }
            }).catch(err => {
                console.log(err);
            })
        },
        FetchCommentsData() {
            var vm = this;
            var params = `id=${vm.ScreenshotDetailArray.screenshoT_ID}&${vm.ScreenShotData.ProjectId}`;
            DataService.GetComments(params).then(response => {
                if(response.data.length > 0){
                    vm.PostedCommentListing = response.data[0].commentList;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        GetDisplayScreen(){
            var vm = this;
            var params = `screenshotId=${vm.ScreenshotDetailArray.screenshoT_ID}&${vm.ScreenShotData.UserId}&${vm.ScreenShotData.ProjectId}`;
            DataService.GetDisplayScreen(params).then(response=>{
                if(response.data != {}){
                    vm.GetDisplayScreenData = response.data;
                }
            })
        },
        DateDifference(ActualDate){
            var vm = this;
            var GetCurrentDate = new Date();
            var GetValueInSeconds = Math.ceil(Math.floor(GetCurrentDate.getTime() - new Date(ActualDate).getTime())/1000);
            console.log('GetCurrentDate',GetCurrentDate,'ActualDate',ActualDate);
            // TO Get Hours
            var hours = Math.floor(GetValueInSeconds / (60 * 60));
            // To Get Minutes
            var divisor_for_minutes = GetValueInSeconds % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            var days = Math.floor(GetValueInSeconds / (3600*24));
            if(GetValueInSeconds < 60){
                return vm.$t('FewSecondsAgo');
            }else if (GetValueInSeconds < 120){
                return vm.$t('minutesago');
            }else if (GetValueInSeconds < 2700){
                return minutes + ' ' + vm.$t('minutesago');
            }else if (GetValueInSeconds < 5400){
                return vm.$t('an') + ' ' + vm.$t('hourago');
            }else if (GetValueInSeconds < 86400){
                return hours + ' ' + vm.$t('hourago');
            }else if (GetValueInSeconds < 172800){
                return vm.$t('Yesterday');
            }else if (GetValueInSeconds < 2592000){
                return days + ' ' + vm.$t('daysago');
            }else if (GetValueInSeconds < 31104000){
                var months = Math.floor(days / 30);
                return months <= 1 ? '1 ' + vm.$t('monthsago') : months + ' ' + vm.$t('monthsago')
            }else{
                var year = Math.floor(days/365);
                return year <= 1 ? '1 ' + vm.$t('yearsago') : year + ' ' + vm.$t('yearsago');
            }
        }
    }
}
</script>