import DataService from '../../services/DataService';
let leftsearchSchema = [
    {
        fieldName: "User",
        fieldType: "ddl-radio",
        fieldIcon: "fa fa-user",
        paramName:"User",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: "Project",
        fieldType: "ddl-radio",
        fieldIcon: "fa fa-suitcase",
        paramName:"projectId",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    }
]
function  bindleftCommonSearchdropdown(instance, filtername) {
    var vm = instance;
    if(filtername == 'CA'){
        DataService.GetUsersByCompanyId().then(response=>{
            response.data.forEach(item=>{
            let obj1={
                name:item.username,
                value:item.user_id
                }
                vm.leftSearchFields[0].listOptions.push(obj1);
            })
        }).catch(err =>{
            console.log(err);
        })
    }
    else if(filtername == 'PM'){
        DataService.ListTeamUsersByPM().then(response=>{
            response.data.forEach(item=>{
            let obj1={
                name:item.user_Name,
                value:item.user_id
                }
                vm.leftSearchFields[0].listOptions.push(obj1);
            })
        })
    }
    DataService.GetProjectListByUserIdAndUserType().then(response=>{
        response.data.forEach(item=>{
        let obj1={
            name:item.projectname,
            value:item.projectid
            }
            vm.leftSearchFields[1].listOptions.push(obj1);
        })
    })
}

export default { leftsearchSchema,bindleftCommonSearchdropdown};