<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid mt-2 p-0">
            <div class="left-menu-tab">
                <leftSearch :fields="leftSearchFields" header="Work Diary" v-on:SetLeftSearch="leftCommonSearch">
                </leftSearch>
            </div>
            <div class="right-content">
                
                <div class="col-lg-12 p-0">
                    <div class="heading-border mb-2 cursor-pointer" id="status" v-on:click="ToggleDailyTimeLine">
                        <h5 class="h5"><span>Daily Timeline <i class="fa fa-angle-down"></i></span></h5>
                    </div>
                </div>
                <div class="col-lg-12 p-0" id="week-stat" :style="ToggleTimeLine">
                    <div class="row">
                        <div id="divclockedhours" class="col-sm-12 col-md-12 col-lg-6 mb-3">
                            <div v-if="noRecordOfClockedHours" class="text-center text-danger d-flex align-items-center justify-content-center no-record" style="height:100px; top:34px;">
                                No record(s) found
                            </div>
                            <div class="border infotrack h-100" v-else>
                                <div class="trackinfo" v-for="GetHours in GetDailyClockedHours"> 
                                    <h2 class="time_info">{{ secondsToTime(GetHours.totalhours) }} </h2>
                                    <h3>Total Time Tracked</h3>
                                    <div class="mb-3 float-left w-100">
                                        <div class="p">
                                            <span class="skyblue">Online Time Tracked:</span>
                                            {{ secondsToTime(GetHours.onlinehours) }}
                                        </div>
                                        <div class="p">
                                            <span class="skyblue">Manual Time Tracked:</span>
                                            {{ secondsToTime(GetHours.offlinehours) }}
                                        </div>
                                    </div>
                                    <div class="float-left w-100 p"><span class="grey-txt">Week</span> {{ secondsToTime(GetHours.thiS_WEEK_HOURS) }} &nbsp; |
                                        &nbsp;<span class="grey-txt">Month</span> {{ secondsToTime(GetHours.thiS_MONTH_HOURS) }}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-6 mb-3" id="divapplicationdata">
                            <div class="border app-time  h-100" id="ApplicationTime">
                                <h3 class="text-center border-bottom">Application Time</h3>
                                <ul class="app-time-info" style="max-height: 130px;overflow-y: auto;min-height:130px;">
                                    <div v-if="noRecordOfApplicationTime" class="text-center text-danger d-flex align-items-center justify-content-center no-record" style="height:100px;">
                                        No record(s) found
                                    </div>
                                    <li v-else v-for="GetTime in GetDailyApplicationTime">
                                        <p :title="GetTime.applicatioN_NAME"><strong>{{ GetTime.applicatioN_NAME }}</strong></p>
                                        <div class="progress prog_bx" :data-duration="GetTime.timE_DURATION" :data-totalduration="GetTime.totaL_DURATION" :data-totalhours="GetTime.totalHours == 0 ? 1 : GetTime.totalHours">
                                            <div class="progress-bar bg-lightgreen" :style="'width:' + FindPercentageOfTotalHours(GetTime.timE_DURATION,GetTime.totaL_DURATION)+'%'" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100"></div>
                                        </div>
                                        <span>{{ secondsToTime(GetTime.timE_DURATION) }}<b>({{ FindPercentageOfTotalHours(GetTime.timE_DURATION,GetTime.totaL_DURATION) + '%' }})</b></span>
                                        <span class="d-block clearfix"></span>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-12 p-0 mb-2">
                    <div class="theme-primary partition  overflow-visible" :class="mobileView ? 'p-actions-collapse': 'p-actions-expand'">
                        <span class="p-name text-white">
                            <a id="prevdate" class="btn btn-primary float-left mr-1" href="javascript:;" v-on:click="GetDate('prevdate')"><i class="fa fa-angle-left"></i></a>
                            <v-date-picker mode="date" v-model="modelinputdate" @input="FetchAllData" class="input-group float-left mr-1" style="max-width:150px;"
                            :attributes='attrs'> 
                                <template v-slot="{ inputValue,togglePopover}">
                                    <input placeholder="MM/DD/YYYY"
                                        class="form-control hasdatetimepicker"
                                        :value="inputValue"
                                        v-on:click="togglePopover()"
                                        readonly
                                    />
                                    <div class="input-group-append" data-target="#txtdatepicker" v-on:click="togglePopover()" data-toggle="datetimepicker" title="" data-original-title="Select Date">
                                    <a class="input-group-text"><i class="fa fa-calendar"></i></a></div>
                                </template>
                            </v-date-picker>
                            <a id="todaydate" class="btn btn-primary float-left mr-1" href="javascript:;" v-on:click="GetDate('todaydate')">Today </a>
                            <a id="nextdate" class="btn btn-primary float-left" href="javascript:;" v-on:click="GetDate('nextdate')"><i class="fa fa-angle-right text-white"></i></a>
                        </span>
                        <span class="p-actions float-right mydbsize">
                            <a class="p-action-btn text-white dash" :title="$t('ACTION_NAME')" v-on:click="ShowAction = true">
                                <em class="fa fa-plus"></em><br>{{ $t('ACTION_NAME') }}
                            </a>
                            <a class="p-action-btn text-white dash" :title="$t('FlaggedScreenshot')" v-on:click="GetFlaggedScreenshotListing">
                                <em class="fa fa-flag"></em><br>{{ $t('FlaggedScreenshot') }}
                            </a>
                        </span>
                        <span class="action-list-box expand" v-if="ShowAction" style="width: 224px; display: block; top: -200px; right: 15px; border-bottom: none !important;">
                            <span class="list-actions w-100 fsm-list" id="action-list" style="font-size: 14px;">
                                <a title="" href="javascript:;" class="actions-onclick" id="chkAll" data-toggle-tooltip="tooltip"  v-on:click="SelectAllScreenshots('chkAll')">
                                    <i class="fa fa-check-square-o text-info action-icon fa-size pr-2"></i>
                                    <span class="text" title="Select All">Select All</span>
                                </a>
                                <a title="" href="javascript:;" class="actions-onclick" id="dechkAll" data-toggle-tooltip="tooltip" v-on:click="SelectAllScreenshots('dechkAll')">
                                    <i class="fa fa-times-circle-o text-secondary action-icon fa-size pr-2"></i>
                                    <span class="text" title="Deselect All">Deselect All</span>
                                </a>
                                <a title="" checkprivilege="Yes" href="javascript:;" class="actions-onclick" id="deleteselected" data-toggle-tooltip="tooltip" v-on:click="SelectAllScreenshots('Delete')">
                                    <i class="fa fa-trash text-danger action-icon fa-size pr-2"></i>
                                    <span class="text" title="Delete">Delete</span>
                                </a>
                                <a title="" href="javascript:;" class="actions-onclick" id="flagselected" type="flag" checkprivilege="Yes" data-toggle-tooltip="tooltip" v-on:click="SelectAllScreenshots('flag')">
                                    <i class="fa fa-flag text-primary action-icon fa-size pr-2"></i>
                                    <span class="text" title="Flag">Flag</span>
                                </a>
                                <a title="" href="javascript:;" class="actions-onclick" id="unflagselected" type="unflag" checkprivilege="Yes" data-toggle-tooltip="tooltip" v-on:click="SelectAllScreenshots('unflag')">
                                    <i class="fa fa-flag-o text-dark action-icon fa-size pr-2"></i>
                                    <span class="text" title="UnFlag">UnFlag</span>
                                </a>
                                <i class="fa fa-times close close-action" id="closeaction" aria-hidden="true" v-on:click="ShowAction = false"></i>
                            </span>
                        </span>
                        <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns" @click="mobileView= !mobileView;"><i class="fa fa-angle-up text-white" ></i></a>
                </span>
                    </div>
                    <div class="listing mt-2" v-if="noRecord">
                        <div class="expand-list">
                        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center ">
                            <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                        </a>
                    </div>
                        <div class="table-responsive table-fix-header">
                            <table class="table table-bordered mt-0" style="width:100%" id="Reportpdf"
                                aria-label="Report Preview Listing">
                                <tbody>
                                    <tr style="cursor:initial;">
                                        <td colspan="4">
                                            <div
                                                class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                {{ $t("NoRecordfound") }}</div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div id="divscreenslist" class="border time_infobx" v-else>
                        <div class="expand-list">
                        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center ">
                            <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                        </a>
                    </div>
                        <div class="col-lg-12 p-0">
                            <div class="heading-border mb-2">
                                <h5 class="h5"><span>{{ $options.filters.formatDate(modelinputdate) }}</span></h5>
                            </div>
                        </div>
                        <div class="col-lg-12 p-0 workdiary_sanp_row">
                            <ul class="timeline">
                                <li class="timeline-inverted" v-for="(Mitem, index) in GetWorkDiaryListing">
                                    <div class="timeline-badge warning workdiary_timecontainer" style="z-index: auto;">
                                        <div class="timeline-check snap_checkbox custom-checkbox">
                                            <input type="checkbox" data-change-id="no" :class="'workdiarylist'+index"
                                                class="masterchk custom-control-input" :id="'ckmainfram-' + Mitem.timeFrame"
                                                :value="Mitem.timeFrame" v-on:change="CheckAllTimeFrame('workdiarylist'+index)">
                                            <label class="snap_time custom-control-label"
                                                :for="'ckmainfram-' + Mitem.timeFrame">{{ Mitem.timeFrame }}</label>
                                        </div>
                                    </div>
                                    <div class="timeline-panel">
                                        <ul>
                                            <li v-for="(item,mindex) in Mitem.ScreenshotListing">
                                                <div class="card-box screenshot">
                                                    <!-- For status  Active and Flagged-->
                                                    <div class="card-heading text-truncate pl-2"
                                                        :title="item.screenshoT_TITLE" v-if="item.statuS_ID == 1001 || item.statuS_ID == 1064">
                                                        <div class="snap_checkbox custom-control custom-checkbox float-left">
                                                            <input type="checkbox" :screenshotids="item.screenshoT_ID"
                                                                flagtype="unflagged" :frame="item.timeFrame"
                                                                :value="item.screenshoT_ID"
                                                                :id="item.screenshoT_ID + '|' + item.screenshoT_TYPE" data-change-id="no"
                                                                class="deletable custom-control-input" :class="'workdiarylist'+index">
                                                            <label class="custom-control-label"
                                                                :for="item.screenshoT_ID + '|' + item.screenshoT_TYPE"></label>
                                                        </div>
                                                        {{ item.screenshoT_TITLE }}
                                                    </div>
                                                    <!-- For status  Active and Flagged-->
                                                    <div class="card-detail-main detail_bx" v-if="item.statuS_ID == 1001 || item.statuS_ID == 1064">
                                                        <div :screenshotid="item.screenshoT_ID"
                                                            class="text-center pb-3 float-left w-100 viewdetails">
                                                            <img loading="lazy" v-on:click="GetScreenshotDetails(item)"
                                                                :src="item.screenshoT_TYPE.toLowerCase() == 'screenshot' || item.screenshoT_TYPE.toLowerCase() == 'webcam' ? 'https://cogniterblob.blob.core.windows.net/'+item.containeR_NAME+'/thumb/' + item.screenshoT_PATH
                                                                : '/Content/images/vidoe-thumb.png'"
                                                                alt="" class="w-100">
                                                        </div>
                                                        <div class="time_clk">
                                                            <p>{{ FormatDateToTime(item.createD_AT) }}</p>
                                                        </div>
                                                        <div class="time_lab" v-if="item.activity > 60">
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                        </div>
                                                        <div class="time_lab" v-else-if="item.activity > 20 && item.activity <= 60">
                                                            <div class="time-prog bg-warning"></div>
                                                            <div class="time-prog bg-warning"></div>
                                                            <div class="time-prog bg-warning"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                        </div>
                                                        <div class="time_lab" v-else="item.activity <= 20">
                                                            <div class="time-prog bg-danger"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                        </div>
                                                        <a class="viewdetails" flagtype="unflagged"
                                                            href="javascript:void(0);"
                                                            :screenshotid="item.screenshoT_ID" v-on:click="GetScreenshotDetails(item)">more...</a>
                                                        <!-- For status Flagged-->
                                                        <i class="fa fa-flag text-danger text-right mt-2 font-20" v-if="item.statuS_ID == 1064"></i>
                                                    </div>
                                                    <!-- For status Deleted Screenshot-->
                                                    <div class="card-heading" v-if="item.statuS_ID == 1058">&nbsp;</div>
                                                    <div class="card-detail-main detail_bx" v-if="item.statuS_ID == 1058">
                                                        <div class="text-center pb-3 float-left w-100">
                                                            <span class="discarded-img" title="Discarded">Discarded Screenshot</span>
                                                        </div>
                                                        <div class="time_clk">
                                                            <p>{{ FormatDateToTime(item.createD_AT)}}</p>
                                                        </div>
                                                        <div class="time_lab" v-if="item.activity > 60">
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                            <div class="time-prog bg-success"></div>
                                                        </div>
                                                        <div class="time_lab" v-else-if="item.activity > 20 && item.activity <= 60">
                                                            <div class="time-prog bg-warning"></div>
                                                            <div class="time-prog bg-warning"></div>
                                                            <div class="time-prog bg-warning"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                        </div>
                                                        <div class="time_lab" v-else="item.activity <= 20">
                                                            <div class="time-prog bg-danger"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                            <div class="time-prog green"></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <PostComment v-if="ShowPostCommentPopup" :PostCommentID="PostCommentID" :projectId="projectId"></PostComment>
        <ScreenshotDetails v-if="ShowScreenshotDetails" :ScreenshotDetailArray="ScreenshotDetailArray" :ScreenShotData="ScreenShotData"></ScreenshotDetails>
    </section>
</template>

<script>
import Schema from "../TimeTracking/WorkDiaryListingSchema";
import DataService from '../../services/DataService';
import leftSearch from "../../components/Common/left-search.vue";
import PostComment from "../../components/Modal/modalPostComment.vue";
import ScreenshotDetails from "../../components/Modal/modalScreenshotDetails.vue";
import moment from 'moment';
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default {
    components: {
        leftSearch,
        PostComment,
        ScreenshotDetails
    },
    data() {
        return {
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: '',
            sortExp: '',
            pageSize: 10,
            pageNum: 1,
            applicationCode: '',
            noRecord: true,
            noRecordOfClockedHours: true,
            noRecordOfApplicationTime: true,
            modelinputdate: new Date().toISOString().split('T')[0],
            WorkDiaryListing: [],
            GetWorkDiaryListing: [],
            Userid: 'User=0',
            projectId: 'projectId=0',
            GetDailyClockedHours: [],
            GetDailyApplicationTime: [],
            ShowAction: false,
            ShowPostCommentPopup:false,
            ShowScreenshotDetails:false,
            PostCommentID:{},
            ScreenshotDetailArray:[],
            ScreenShotData:{},
            ToggleTimeLine: 'display: block;',
            attrs: [
                {
                    highlight: {
                        style: {
                        background: ''
                        },
                        contentStyle: {
                        color: ''
                        }
                    },
                    dates: null
                },
            ],
            mobileView:false
        }
    },
    async created() {
        //this.setTourGuideSteps(0);
        await this.GetClockedHoursDetails();
        await this.GetApplicationTime();
        await this.FetchData();
        await this.FetchMonthlyScreenshotData();
        Schema.bindleftCommonSearchdropdown(this, GetUserInfo.value.UserType);
    },
    mounted() {
        Schema.leftsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        async FetchAllData() {
            var vm = this;
            if (vm.modelinputdate != null) {
                await vm.GetClockedHoursDetails();
                await vm.GetApplicationTime();
                await vm.FetchData();
                await vm.FetchMonthlyScreenshotData();
            }
        },
        async FetchData() {
            var vm = this;
            vm.isLoading = true;
            var ele=document.getElementsByTagName("input");  
            for(var i=0; i<ele.length; i++){  
                if(ele[i].type=='checkbox')  
                    ele[i].checked=false;  
            }
            var getsearchdate = null;
            if (vm.modelinputdate != null) {
                getsearchdate = vm.$options.filters.formatDate(vm.modelinputdate);
            }
            var params = `${vm.projectId}&search=${getsearchdate}&flagStatus=null&WorkingLanguage=null&${vm.Userid}`;
            await DataService.GetAllData(params).then(response => {
                if (response.data.listing.length > 0) {
                    vm.WorkDiaryListing = response.data.listing;
                    var tempvalue = '';
                    var tempScreenshotArray = [];
                    vm.GetWorkDiaryListing = [];
                    for (let i = 0; i < vm.WorkDiaryListing.length; i++) {
                        if (tempvalue == '') {
                            tempvalue = vm.WorkDiaryListing[i].timeFrame;
                            tempScreenshotArray.push(vm.WorkDiaryListing[i]);
                            vm.GetWorkDiaryListing.push({
                                'timeFrame': tempvalue,
                                'ScreenshotListing': tempScreenshotArray
                            })
                        }
                        else if (tempvalue == vm.WorkDiaryListing[i].timeFrame) {
                            let getindex = vm.GetWorkDiaryListing.findIndex(x => x.timeFrame == tempvalue);
                            tempScreenshotArray.push(vm.WorkDiaryListing[i]);
                            vm.GetWorkDiaryListing[getindex].ScreenshotListing = tempScreenshotArray.sort((FirstValue, SecondValue) => FirstValue.createD_AT.localeCompare(SecondValue.createD_AT))
                        } else {
                            tempvalue = '';
                            tempScreenshotArray = [];
                            tempvalue = vm.WorkDiaryListing[i].timeFrame;
                            tempScreenshotArray.push(vm.WorkDiaryListing[i]);
                            vm.GetWorkDiaryListing.push({
                                'timeFrame': tempvalue,
                                'ScreenshotListing': tempScreenshotArray
                            })
                        }
                    }
                    vm.isLoading = false;
                    vm.noRecord = false;
                } else {
                    vm.GetWorkDiaryListing = [];
                    vm.noRecord = true;
                    vm.isLoading = false;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        FormatDateToTime(Getdate) {
            return moment(Getdate).format('LT');
        },
        async leftCommonSearch(value) {
            var vm = this;
            if(value.searchConditionJson != ""){
                if(value.searchConditionJson.includes('User') && value.searchConditionJson.includes('projectId')){
                    let getbothvalue = value.searchConditionJson.split('&');
                    vm.Userid = getbothvalue[0];
                    vm.projectId = getbothvalue[1];
                }else{
                    if (value.searchConditionJson.includes('projectId')) {
                        vm.projectId = value.searchConditionJson;
                    }else {
                        vm.Userid = value.searchConditionJson;
                    }
                }
            }else{
                vm.Userid = "";
                vm.projectId = "";
            }
            // vm.modelinputdate = null;
            await vm.GetClockedHoursDetails();
            await vm.GetApplicationTime();
            await vm.FetchData();
            await vm.FetchMonthlyScreenshotData();
        },
        async GetClockedHoursDetails(){
            var vm = this;
            vm.isLoading = true;
            var getsearchdate = null;
            if (vm.modelinputdate != null) {
                getsearchdate = vm.$options.filters.formatDate(vm.modelinputdate);
            }
            var params = `${vm.Userid}&${vm.projectId}&search=${getsearchdate}`;
            await DataService.GetClockedHours(params).then(response=>{
                if(response.data != null){
                    vm.GetDailyClockedHours = [];
                    vm.GetDailyClockedHours.push(response.data);
                    vm.noRecordOfClockedHours = false;
                    vm.isLoading = false;
                }else{
                    vm.noRecordOfClockedHours = true;
                    vm.isLoading = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        async GetApplicationTime(){
            var vm = this;
            var getsearchdate = null;
            vm.isLoading = true;
            if (vm.modelinputdate != null) {
                getsearchdate = vm.$options.filters.formatDate(vm.modelinputdate);
            }
            var params = `screenshotId=0&${vm.Userid}&${vm.projectId}&langkey=en&search=${getsearchdate}&duration=0&totalHours=0`;
            await DataService.GetApplicationTracking(params).then(response=>{
                if(response.data.length > 0){
                    vm.GetDailyApplicationTime = response.data;
                    vm.noRecordOfApplicationTime = false;
                    vm.isLoading = false;
                }else{
                    vm.noRecordOfApplicationTime = true;
                    vm.isLoading = false;
                }
            }).catch(err =>{
                console.log(err);
            })
        },
        secondsToTime(secs){
            // TO Get Hours
            var hours = Math.floor(secs / (60 * 60));
            // To Get Minutes
            var divisor_for_minutes = secs % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            // To Get Seconds
            var divisor_for_seconds = divisor_for_minutes % 60;
            var seconds = Math.ceil(divisor_for_seconds);

            var obj = hours + 'H ' + minutes + 'M ' + seconds + 'S';
            return obj;
        },
        FindPercentageOfTotalHours(TimeDuration,TotalDuration){
            return ((TimeDuration/ TotalDuration) * 100).toFixed(2);
        },
        async GetDate(e){
            var vm = this;
            if(e == 'prevdate'){
                vm.modelinputdate = moment(moment(vm.modelinputdate).subtract(1, 'days')).format("MM/DD/YYYY")
            }else if(e == 'todaydate'){
                vm.modelinputdate = new Date().toISOString().split('T')[0];
            }else if(e == 'nextdate'){
                vm.modelinputdate = moment(moment(vm.modelinputdate).add(1, 'days')).format("MM/DD/YYYY")
            }
            await vm.FetchAllData();
        },
        SelectAllScreenshots(GetID){
            var vm = this;
            var checkedValue = [];
            var getvalue = []
            var getScreenshotids = document.getElementsByClassName('deletable custom-control-input');
            for(var i=0; getScreenshotids[i]; ++i){
                if(getScreenshotids[i].checked){
                    checkedValue.push(getScreenshotids[i].id);
                    getvalue.push(getScreenshotids[i].value);
                }
            }
            if(GetID == 'chkAll'){
                var ele=document.getElementsByTagName("input");  
                for(var i=0; i<ele.length; i++){  
                    if(ele[i].type=='checkbox')  
                        ele[i].checked=true;  
                } 
            }else if(GetID == 'dechkAll'){
                var ele=document.getElementsByTagName("input");  
                for(var i=0; i<ele.length; i++){  
                    if(ele[i].type=='checkbox')  
                        ele[i].checked=false;  
                } 
            }else if(GetID == 'Delete'){
                if(checkedValue.length > 0){
                    vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("ConfirmDelete")), function () {
                        var params = `screenids=${checkedValue.join(',')}`;
                        DataService.Deletescreenshots(params).then(response=>{
                            if(response.data){
                                vm.ShowAlert(vm.$t("DeleteScreenSuccess"), 'success', true, vm.$t('Alert'));
                                vm.FetchData();
                            }else{
                                vm.ShowAlert(vm.$t("DeleteScreenError"), 'failure', true, vm.$t('Alert'));
                            }
                        }).catch(err =>{
                            console.log(err);
                        })
                    })
                }else{
                    vm.ShowAlert(vm.$t("SelectScreenshot"), 'warning', true, vm.$t('Alert'));
                }
            }else if(GetID == 'flag'){
                if(checkedValue.length > 0){
                    vm.confirmR(vm.$t("ConfirmFlagRecords"), true, (vm.$t("ConfirmFlagRecords")), function () {
                        var params = `screenids=${checkedValue.join(',')}&type=${GetID}`;
                        DataService.FlagScreenShots(params).then(response=>{
                            if(response.data){
                                vm.PostCommentID = {
                                    'ScreenshotIDwithType' : checkedValue.join(','),
                                    'ScreenshotIDwithoutType' : getvalue.join(',')
                                };
                                vm.FetchData();
                                vm.ShowPostCommentPopup = true;
                            }else{
                                vm.ShowAlert(vm.$t("FlagScreenError"), 'success', true, vm.$t('Alert'));
                            }
                        }).catch(err=>{
                            console.log(err);
                        })
                    })
                }else{
                    vm.ShowAlert(vm.$t("SelectFlag"), 'warning', true, vm.$t('Alert'));
                }
            }else if(GetID == 'unflag'){
                if(checkedValue.length > 0){
                    vm.confirmR(vm.$t("ConfirmUpdationUserBreak"), true, (vm.$t("ConfirmUpdationUserBreak")), function () {
                        var params = `screenids=${checkedValue.join(',')}&type=${GetID}`;
                        DataService.FlagScreenShots(params).then(response=>{
                            if(response.data){
                                vm.PostCommentID = {
                                    'ScreenshotIDwithType' : checkedValue.join(','),
                                    'ScreenshotIDwithoutType' : getvalue.join(',')
                                };
                                vm.FetchData();
                                vm.ShowPostCommentPopup = true;
                            }else{
                                vm.ShowAlert(vm.$t("FlagScreenError"), 'success', true, vm.$t('Alert'));
                            }
                        }).catch(err=>{
                            console.log(err);
                        })
                    })
                }else{
                    vm.ShowAlert(vm.$t("UnflagRecord"), 'warning', true, vm.$t('Alert'));
                }
            }
        },
        ClosePostComment(){
            var vm = this;
            vm.PostCommentID = '';
            vm.ShowPostCommentPopup = false;
        },
        GetFlaggedScreenshotListing(){
            this.$router.push({path:'/TimeTracking/FlaggedScreenshot'});
        },
        GetScreenshotDetails(value){
            var vm = this;
            vm.ShowScreenshotDetails = true;
            vm.ScreenshotDetailArray = value;
            vm.ScreenShotData = {
                'UserId': vm.Userid,
                'ProjectId': vm.projectId,
                'InputDate': vm.modelinputdate
            }
            $("body").addClass("modal-open");
        },
        CloseScreenshotDetails(){
            var vm = this;
            vm.ShowScreenshotDetails = false;
            $("body").removeClass("modal-open");
        },
        CheckAllTimeFrame(GetCheckBoxId){
            var array = document.getElementsByTagName("input");
            for(var i = 0; i < array.length; i++){
                if(array[i].type == "checkbox"){
                    if(array[i].className == 'deletable custom-control-input ' + GetCheckBoxId){
                        var getScreenshotids = document.getElementsByClassName('masterchk custom-control-input ' + GetCheckBoxId);
                        if(getScreenshotids[0].checked){
                            array[i].checked = true;
                        }else{
                            array[i].checked = false;
                        }
                    }
                }
            }
        },
        async FetchMonthlyScreenshotData(){
            var vm = this;
            var getDate = moment(vm.modelinputdate, 'YYYY/MM/DD');
            var getMonth = getDate.format('M');
            var getYear = getDate.format('YYYY');
            var params = `${vm.Userid}&${vm.projectId}&month=${getMonth}&year=${getYear}`;
            // var params = `${vm.projectId}&month=${getMonth}&year=${getYear}`;
            vm.attrs = [];
            await DataService.GetScreenshotDates(params).then(response=>{
                if(response.data.length > 0){
                    response.data.forEach(x=>{
                        var getFormattedDate = moment.utc(x.selecteddate);
                        vm.attrs.push({
                            highlight: {
                            style: {
                            background: '#ff8080'
                            },
                            contentStyle: {
                            color: 'black'
                            }
                        },
                        dates: new Date(parseInt(getYear), parseInt(getDate.format('M')) - 1, parseInt(getFormattedDate.format('D')))
                        })
                    })
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        ToggleDailyTimeLine(){
            var vm = this;
            if(vm.ToggleTimeLine == 'display: none;'){
               vm.ToggleTimeLine = 'display: block;';
            }else{
               vm.ToggleTimeLine = 'display: none;'
            }
        }
    }
}
</script>